		font: 12px Helvetica Neue,Helvetica,Arial,Microsoft Yahei,Hiragino Sans GB,Heiti SC,WenQuanYi Micro Hei,sans-serif;
	}
	.filter-wrap{
		height: auto;
		padding: 20px 0 10px;
		position: relative;
		border-bottom: 1px solid #e5e9ef;
		display: table;
		width: 100%;
	}
	.filter-wrap .filter-type{
		margin-bottom: 10px;
	}
	li, ol, ul{
		margin: 0;
		padding: 0;
		list-style: none;
	}
	.filter-wrap .filter-item.active{
		background-color:#00a1d6;

	}
	.filter-wrap .filter-item.active a{
		color: white;
	}
	.filter-wrap .filter-item{
		float: left;
		padding-left: 8px;
		padding-right: 8px;
		border-radius: 4px;
		margin-right: 15px;
	}
	.filter-wrap .filter-item a{
		font-size: 12px;
		line-height: 20px;
	}
	.filter-wrap .filter-type.tids.filter-item{
		margin-right: 8px;
	}
	.video.matrix:nth-child(5n){
		margin-right: 0px!important;
	}
	a:hover .cover{
		background: #979797;opacity: 0.4;
	}
	.so-icon{
		display: inline-block;
		font-size: 12px;
		height: 12px;
		vertical-align: text-top;
		line-height: 12px;
		padding-left: 16px;
		position: relative;
		color: #99a2aa;
		margin-right: 8px;
		margin-bottom: 12px;
		float: left;
	}
	 .info .so-icon.watch-num{
	 	width: 41px;
	 	overflow: inherit;
	 }
	  .hide{
	 	display: none;
	 }
	 .info .time{
	 	margin-right: 0;
	 	width: 67px;
	 }
	 .so-icon i{
	 	position: absolute;
	 	left: 0;
	 	top: 0;
	 	background-image: url();
	 	width: 11px;
	 	height: 11px;
	 }
	 .so-icon i.icon-playtime{
	 	background-position: -485px -543px;
	 }
	 .so-icon i.icon-subtitle{
	 	background-position:-442px -124px;
	 }
	 .so-icon i.icon-date{
	 	background-position:-442px -165px;
	 }
	 .so-icon i.icon-uper{
	 	background-position:-442px -83px;
	 }
</style>


