import Vue from 'vue'
import VueRouter from 'vue-router'

import whole from './components/bilibili_search/whole.vue'
import "babel-polyfill";

Vue.config.productionTip = false
Vue.use(VueRouter)
new Vue({
	el:'#app',
	template:'<whole />',
	components:{
		whole:whole
	}
})






