<template>
	<div class="bili-header-m report-wrap-module report-wrap-module" style="box-shadow:0 1px 2px rgba(0,0,0,.1)">
		<div class="nav-menu">
			<div class="nav-wrapper clearfix bili-wrapper">
				<div class="nav-con fl">
					<ul>
						<li class="nav-item home" v-for="item in title">
							<a href="javascripy:void()" class="t" :class="item.icon">
								<i v-if="item.icon!='a'" :class="item.icon" class="icon"></i>
								{{item.text}}
							</a>
						</li>
					</ul>
				</div>
				<div class="up-load fr">
					<a href="javascript:void()" class="u-link">投稿</a>
				</div>
				<div class="nav-con fr">
					<ul class="fr">
						<li class="nav-item profile-info">
							<div style="position: absolute;top: 0; bottom: 0; left: 0; right: 0; margin: auto;height: 32px;width: 32px;">
							<img src="static/1.png" style="display: block;height: 100%;border-radius:50%;"/>
						</div>
						</li>
						<li class="nav-item" v-for="item in second">
							<a href="javascript:void()"class="t">{{item}}</a>
						</li>
					</ul>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default{
		data:function(){
			return{
				title:[
				{icon:"first",text:"主站"},
				{icon:"a",text:"音频"},
				{icon:"a",text:"游戏中心"},
				{icon:"a",text:"直播"},
				{icon:"a",text:"会员购"},
				{icon:"a",text:"漫画"},
				{icon:"a",text:"BW"},
				{icon:"a",text:"70年"},
				{icon:"download",text:"下载APP"},
				],
				second:['大会员','消息','动态','稍后再看','收藏','历史']
			}
		}
	}
</script>

<style>
	@font-face{font-family:header-iconfont;src:url(//s1.hdslb.com/bfs/static/jinkela/search/asserts/iconfont.eot);src:url(//s1.hdslb.com/bfs/static/jinkela/search/asserts/iconfont.eot#iefix) format("embedded-opentype"),url("data:application/x-font-woff;charset=utf-8;base64,") format("woff"),url(//s1.hdslb.com/bfs/static/jinkela/search/asserts/iconfont.ttf) format("truetype"),url(//s1.hdslb.com/bfs/static/jinkela/search/asserts/iconfont.svg#header-iconfont) format("svg")}.header-iconfont{font-family:header-iconfont!important;font-size:16px;font-style:normal;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}.header-icon-music:before{content:"\E601"}.header-icon-game:before{content:"\E603"}.header-icon-dance:before{content:"\E605"}.header-icon-zhibo:before{content:"\E606"}.header-icon-ent:before{content:"\E607"}.header-icon-life:before{content:"\E608"}.header-icon-kichiku:before{content:"\E609"}.header-icon-guochuang:before{content:"\E60A"}.header-icon-fashion:before{content:"\E60B"}.header-icon-ad:before{content:"\E60C"}.header-icon-anime:before{content:"\E60D"}.header-icon-guochandonghuatuijian:before{content:"\E612"}.header-icon-technology:before{content:"\E6AD"}.header-icon-general_pullup_s:before{content:"\E6EC"}.header-icon-Navbar_mobile:before{content:"\E724"}.header-icon-Navbar_logo:before{content:"\E725"}.header-icon-bilibili-tv:before{content:"\E668"}.header-icon-general_upload:before{content:"\E634"}.header-icon-general_search:before{content:"\E635"}.header-icon-activit:before{content:"\E63B"}.header-icon-blackroom:before{content:"\E63F"}.header-icon-read:before{content:"\E640"}.header-icon-ic_partition_broadca:before{content:"\E641"}.header-icon-cinephile:before{content:"\E642"}.header-icon-cinema:before{content:"\E643"}.header-icon-topic:before{content:"\E644"}.header-icon-douga:before{content:"\E645"}.header-icon-digital:before{content:"\E64E"}

	*{
		padding: 0;
		margin: 0;
	}
	.bili-header-m, .bili-header-m article, .bili-header-m aside, .bili-header-m blockquote, .bili-header-m button, .bili-header-m code, .bili-header-m dd, .bili-header-m details, .bili-header-m dl, .bili-header-m dt, .bili-header-m fieldset, .bili-header-m figcaption, .bili-header-m figure, .bili-header-m footer, .bili-header-m form, .bili-header-m h1, .bili-header-m h2, .bili-header-m h3, .bili-header-m h4, .bili-header-m h5, .bili-header-m h6, .bili-header-m header, .bili-header-m hgroup, .bili-header-m hr, .bili-header-m input, .bili-header-m legend, .bili-header-m li, .bili-header-m menu, .bili-header-m nav, .bili-header-m ol, .bili-header-m p, .bili-header-m pre, .bili-header-m section, .bili-header-m td, .bili-header-m textarea, .bili-header-m th, .bili-header-m ul{
		margin: 0;
		padding: 0;
	}
	.bili-header-m{
		font:12px Helvetica Neue,Helvetica,Arial,Microsoft Yahei,Hiragino Sans GB,Heiti SC,WenQuanYi Micro Hei,sans-serif;
		position: relative;
		z-index: 10000;
		background: #FFFFFF;
	}
	.bili-header-m .nav-menu{
		position: relative;
		z-index: 200;
		height: 42px;
		color: #222;
	}
	.bili-header-m .bili-wrapper{
		width: 980px;
		margin: 0 auto;
		
	}
	.bili-header-m .fl{
		float: left;
	}
	.bili-header-m ol, .bili-header-m ul{
		list-style: none;
	}
	.bili-header-m .nav-menu .nav-con .nav-item.home{
		margin-left: -10px;
		padding-left: 12px;
	}
	.bili-header-m .nav-menu .nav-con .nav-item{
		float: left;
		text-align: center;
		line-height: 42px;
		height: 42px;
		position: relative;
		background-color:hsla(0,0%,100%,0) ;
	}
	.bili-header-m .nav-menu .nav-con .nav-item.home a{
		color:#222;
		height: 100%;
		display: block;
		padding: 0 7px;
	}
	.bili-header-m .nav-menu .nav-con .nav-item a{
		color:#222;
		height: 100%;
		display: block;
		padding: 0 7px;
	}
	.bili-header-m .nav-menu .nav-con .nav-item.home a.first{padding-left: 20px;}
	.bili-header-m .nav-menu .nav-con .nav-item.home a.download{padding-left: 15px;}
	
	i.icon{
		font-family: header-iconfont!important;
		position: absolute;
		left: 9px;
		top: -1px;
		color: #00a1d6;
		font-size: 20px;
		font-style: normal;
		
	}
	i.download{
		font-size: 17px;
		
	}
	.bili-header-m .nav-menu .nav-con .nav-item.home a i.first:before{
		content: "\E668";
	}
	.bili-header-m .nav-menu .nav-con .nav-item.home a i.download:before{
		content: "\E724";
	}
	.bili-header-m .nav-menu .up-load{
		position: relative;
		width: 68px;
		height: 42px;
		margin-left: 8px;
	}
	.bili-header-m .fr{
		float: right;
	}
	.bili-header-m .fl, .bili-header-m .fr{
		display: inline;
	}
	.u-link{
		display: block;
		width: 100%;
		height: 46px;
		line-height: 42px;
		text-align: center;
		font-size: 14px;
		color: #FFFFFF;
		background: #f45a8d;
		border-radius: 0 0 6px 6px;
	}
	a{
		text-decoration: none;
	}
	.bili-header-m .nav-menu .nav-con .profile-info{
		width: 46px;
	}
</style>


